@import "bootstrap-rating";
@font-face {
  font-family:'Unthrift First Personal';
  src:url(/fonts/unthriftfirstpersonal.eot);
  src:url(/fonts/unthriftfirstpersonal.woff2) format("woff2"),url(/fonts/unthriftfirstpersonal.woff) format("woff"),url(/fonts/unthriftfirstpersonal.ttf) format("truetype"),url(/fonts/unthriftfirstpersonal.svg) format("svg")
}

body{
  font-family: 'Merriweather', serif;
}

.custom-star {
  color: #309DE1;
}

.favourite {
  color: #5cb85c;
}

#head-c {
  text-align: center;
  margin-top: 25px;
  margin-bottom: 40px;
}

#head-c h1 {
  font-family: 'Unthrift First Personal' !important;
  font-size: 60px;
  margin-bottom: 0;
  color: #818a91;
}

.info-div{
  position: relative;
  width: 250px;
  padding-top: 5px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  background-color: #fff;
  background-color: rgba(255,255,255,.8);
  text-align: left;
}
.wrapper{
  box-sizing: border-box;
  height: 100%;
  position: relative;
  padding: 25px 0;
  padding-left: 15px;
  margin: 0;
  z-index: 1;
}

.info-list{
  margin: 0;
  padding: 0;
}

.info-list li{
  list-style: none;
  margin-top: 10px;
}

.ingredients-list{
  list-style: outside none none;
  padding: 0px;
  padding-top: 15px;
  font-size: 15pt;
}


.ingredients-list li:first-child {
  border-top: 1px dashed #B9B9B4;
}

.ingredients-list li{
  padding: 0px;
  border-bottom: 1px dashed #B9B9B4;
  line-height: 200%;
}

.navbar {
  margin-bottom: 0;
}

.navbar-collapse .container-fluid {
  padding: 2rem 2.5rem;
  border-bottom: 1px solid #55595c;
}

.navbar-collapse h4 {
  color: #818a91;
}

.navbar-collapse .text-muted {
  color: #818a91;
}

.about {
  float: left;
  max-width: 30rem;
  margin-right: 3rem;
}

.social a {
  font-weight: 500;
  color: #eceeef;
}
.social a:hover {
  color: #fff;
}

a {
  color: #797979;
  text-decoration: none;
}

a:focus, a:hover {
  color: #5cb85c;
  text-decoration: none;
}

a:focus {
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}

.recipe {
  padding-top: 6rem;
  padding-bottom: 3rem;
  background-color: rgb(241, 241, 241);
}

.card-img {
  border-radius: 0.25rem;
  width: 100%;
}

.card-img-top {
  border-radius: 0.25rem;
  width: 100%;
}

.card-footer{
  position:absolute;
  bottom:0;
  width:100%;
}
.card-deck .card{
  padding-bottom:0px;
}

.card-block{
  margin-bottom: 150px;
}

.card:hover {
  box-shadow: 0 0 4px #999;
}

.card > img {
  margin-bottom: .75rem;
}


footer {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

footer p {
  margin-bottom: .25rem;
}

